nav .link__items {
  padding: var(--xs) 0;
}

nav .nav__links.flex {
  justify-content: center;
  flex-direction: row;
}

nav .link__item {
  color: var(--light-color);
  padding: var(--xs);
  border-radius: var(--xs);
  font-size: var(--s);
  transition: all 500ms;
}

nav .link__items:nth-child(5) .link__item {
  background-color: var(--secondary-color);
}

nav .link__item:hover {
  background-color: var(--secondary-color);
}

@media (min-width: 425px) {
}

@media (min-width: 720px) {
  nav .nav__links.flex {
    justify-content: center;
    flex-direction: row;
  }
  nav .link__items {
    padding: var(--m) 0;
  }
  nav .link__item {
    padding: var(--s);
    font-size: var(--m);
  }
}

@media (min-width: 1024px) {
}
