@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

a {
  color: var(--dark-color);
  text-decoration: none;
}

:root {
  --primary-color: #235789;
  --secondary-color: #c1292e;
  --dark-color: #000;
  --light-color: #fff;
  --max-width: 78em;

  --xs: 0.5em;
  --s: 0.8em;
  --m: 1em;
  --l: 1.2em;
  --xl: 1.5em;
}

body {
  background-color: #fff;
  background-color: var(--light-color);
  color: #000;
  color: var(--dark-color);
  font-family: "Montserrat", sans-serif;
}

.container {
  max-width: 78em;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 1em;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 300ms;
}

header .container {
  padding: 0;
}

.header__title {
  color: var(--light-color);
}

header .logo {
  width: 200px;
}

.header .flex {
  flex-direction: column;
}

.--scroll-nav {
  background-color: var(--primary-color);
}

@media (min-width: 425px) {
}

@media (min-width: 720px) {
  .header .flex {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 1024px) {
}

nav .link__items {
  padding: var(--xs) 0;
}

nav .nav__links.flex {
  justify-content: center;
  flex-direction: row;
}

nav .link__item {
  color: var(--light-color);
  padding: var(--xs);
  border-radius: var(--xs);
  font-size: var(--s);
  transition: all 500ms;
}

nav .link__items:nth-child(5) .link__item {
  background-color: var(--secondary-color);
}

nav .link__item:hover {
  background-color: var(--secondary-color);
}

@media (min-width: 425px) {
}

@media (min-width: 720px) {
  nav .nav__links.flex {
    justify-content: center;
    flex-direction: row;
  }
  nav .link__items {
    padding: var(--m) 0;
  }
  nav .link__item {
    padding: var(--s);
    font-size: var(--m);
  }
}

@media (min-width: 1024px) {
}

footer {
  background-color: var(--secondary-color);
  color: var(--light-color);
}

.disclaimer {
  text-align: center;
}

.jumbotron {
  display: flex;
  justify-content: center;
  align-items: center;
}

.jumbotron__content {
  text-align: center;
  background: #0009;
  padding: var(--m);
  width: 300px;
  margin-top: 1em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.jumbotron__profile-pic {
  background-color: var(--light-color);
  border: 1px solid #3333;
  border-radius: 50%;
  padding: var(--xs);
  width: 250px;
  margin: 0px auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.jumbotron__links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.jumbotron__links .link__items {
  padding: var(--m);
}
.jumbotron__links .link__item {
  padding: var(--m);
  border-radius: var(--xs);
  color: var(--light-color);
}

.jumbotron__links .link__item:hover {
  background-color: var(--secondary-color);
}

@media (min-width: 425px) {
}

@media (min-width: 720px) {
  .jumbotron__content {
    width: 500px;
  }
}

@media (min-width: 1024px) {
}


#project {
  background: var(--secondary-color);
}

.project__info {
  background-color: #fff;
  padding: 1em;
  min-height: 160px;
}

.--active {
  background-color: var(--primary-color);
  padding: var(--s);
  font-size: var(--m);
  color: var(--light-color);
  border-radius: var(--xs);
  display: inline-block;
}

.--active:hover {
  background-color: var(--secondary-color);
}

.project__items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 1em;
}

@media (min-width: 425px) {
}

@media (min-width: 720px) {
  .project__items {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .project__items {
    grid-template-columns: repeat(3, 1fr);
  }
}

.contact-form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: stretch;
}

.contact-form label {
  padding: var(--m) 0;
}
.contact-form input {
  padding: var(--m) var(--xs);
  border: none;
  background-color: aliceblue;
}

.contact-form textarea {
  padding: var(--m) var(--xs);
  border: none;
  background-color: aliceblue;
}

.contact-form .submit {
  cursor: pointer;
  margin-top: var(--m);
  background-color: var(--primary-color);
  color: var(--light-color);
  font-size: var(--xl);
}

.contact-form .submit:hover {
  background-color: var(--secondary-color);
}

